class User {
  static async auth(login = '', password = '') {
    const userModule = await import('../data/users.js')
    const userData = userModule.default.users
    return Boolean(login == userData.login && password == userData.password)
  }
  static async getBalconies() {
    const balconies = await import('../data/scene.js')
    return balconies.default.scenes
  }
}

export default User