<template>
  <div class="border p-5">
      <label for="login" class="form-label">Логин</label>
      <div class="input-group mb-3">
        <input type="text" class="form-control" placeholder="Логин" aria-label="Логин" id="login" v-model="login">
    </div>

    <label for="password" class="form-label">Пароль</label>
    <div class="input-group mb-3">
      <input :type="passwordInputType"  class="form-control" placeholder="Пароль" aria-label="Пароль" id="password" v-model="password">
      <button class="btn btn-outline-secondary" type="button" id="button-addon2" 
              @click="togglePassword"> {{passwordBtnText}} </button>
    </div>
  
    <button class="btn btn-primary" type="button" :disabled=!isValid @click="auth">Вход</button>
  </div>
</template>

<script>
import UserServices from '../services/User'

export default {
  name: 'Login',
  data() {
    return {
      login: '',
      password: '',
      showPassword: false,
      passwordInputType: 'password',
      passwordBtnText: 'Показать'
    }
  },
  computed: {
    isValid() {
      return Boolean(this.login && this.password)
    } 
  },
  methods: {
    togglePassword() {
      this.showPassword = !this.showPassword
      this.passwordInputType = (this.showPassword) ? 'text' : 'password'
      this.passwordBtnText = (this.showPassword) ? 'Скрыть' : 'Показать'
    },
    async auth() {
     const isAuth = await UserServices.auth(this.login, this.password)
     if (isAuth) {
       this.$router.push('balconies')
     } else {
       alert('Неверные данные')
     }
    }
  }
}
</script>
